import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {

    const open_btn = document.querySelector('.open-btn')
    const close_btn = document.querySelector('.close-btn')
    const link_acasa = document.querySelector('.link_acasa')
    const link_despre = document.querySelector('.link_despre')
    const link_produse = document.querySelector('.link_produse')
    const link_contact = document.querySelector('.link_contact')
    const nav = document.querySelectorAll('.nav')

    open_btn.addEventListener('click', () => {
      nav.forEach(nav_el => nav_el.classList.add('visible'))
    })
    close_btn.addEventListener('click', () => {
      nav.forEach(nav_el => nav_el.classList.remove('visible'))
    })

    link_acasa.addEventListener('click', () => {
      nav.forEach(nav_el => nav_el.classList.remove('visible'))
    })
    link_despre.addEventListener('click', () => {
      nav.forEach(nav_el => nav_el.classList.remove('visible'))
    })
    link_produse.addEventListener('click', () => {
      nav.forEach(nav_el => nav_el.classList.remove('visible'))
    })
    link_contact.addEventListener('click', () => {
      nav.forEach(nav_el => nav_el.classList.remove('visible'))
    })
  }

}
